<template>
  <div class="right">
    <div class="title"><strong>区块链信息</strong></div>
    <br>
    <br>
    <br>
    <div class="box2">
    <!-- <div class="historyTr">
      <div class="head"> span ${result.hash}</div>
      <div class="body"></div>
      <div class="trDate">2024-04-09</div>
    </div> -->
    </div>
    <br>
    <br>
    <br>
  </div>
</template>

<script>
import Web3 from 'web3';
export default {
  name:'DP-HistoryTrView',
  data() {
    return {
      
    }
  },
  methods: {
    hextoString(str) {
      var out = "", len = str.length/2;
      for(var i = 0; i < len; i++){
          out += String.fromCharCode(parseInt(str.substr(i*2,2),16));  
        }
        // 解决乱码问题
      return this.utf8to16(out)
    },
    utf8to16(str) {
      var out, i, len, c;
      var char2, char3;
      out = "";
      len = str.length;
      i = 0;
      while(i < len) {
        c = str.charCodeAt(i++);
        switch(c >> 4){ 
          case 0: case 1: case 2: case 3: case 4: case 5: case 6: case 7:
                out += str.charAt(i-1);
          break;
          case 12: case 13:
              char2 = str.charCodeAt(i++);
              out += String.fromCharCode(((c & 0x1F) << 6) | (char2 & 0x3F));
          break;
          case 14:
                char2 = str.charCodeAt(i++);
                char3 = str.charCodeAt(i++);
                out += String.fromCharCode(((c & 0x0F) << 12) |
                ((char2 & 0x3F) << 6) |
                ((char3 & 0x3F) << 0));
            break;
          }
      }
      return out
    }
  },

  mounted() {
    const web3 = new Web3(new Web3.providers.HttpProvider("HTTP://127.0.0.1:7545"))
    web3.eth.getBlockNumber().then(index => {
      index = Number(index)
      const box2 = document.querySelector('.box2')
      while (index >= 0) {
        web3.eth.getBlock(index).then(block => {
          if (block.transactions === undefined) return
          const transactions = block.transactions
          console.log(transactions)
          transactions.forEach(element => {
            web3.eth.getTransaction(element).then(transaction => {
              if (transaction.data !== '0x') {
                const hexData = transaction.data
                const jsonData = this.hextoString(hexData.substring(2, hexData.length))
                try{
                  const data = JSON.parse(jsonData)
                  box2.innerHTML += `<div class="historyTr" style="border: 1px solid black; border-radius: 5px;">
                  <div class="head" style="border: 1px solid black; border-radius: 5px;"><span style="position: absolute; left:-84px;">区块哈希:</span>${block.hash}</div>
                  <div class="body">
                    <div style="border: 1px solid black; border-radius: 5px; margin: 5px 0;"><span style="position: absolute; left:-84px;">事务类型:</span>${data.type}</div>
                    <div style="border: 1px solid black; border-radius: 5px; margin: 5px 0;"><span style="position: absolute; left:-57px;">参数1:</span>${data.para1}</div>
                    <div style="border: 1px solid black; border-radius: 5px; margin: 5px 0;"><span style="position: absolute; left:-57px;">参数2:</span>${data.para2}</div>
                    <div style="border: 1px solid black; border-radius: 5px; margin: 5px 0;"><span style="position: absolute; left:-48px;">签名:</span>${data.sign}</div>
                  </div>
                  <div class="trDate" style="border-radius: 5px; margin-bottom: 20px;">时间戳: ${data.ts}</div>
                </div>`
                }catch{
                  console.log(111)
                }
                
              }
            })
          })
        })
        index--
      }
    })
  }
}
</script>

<style scoped>
body {
  background: url("../../images/R-C.jpg") no-repeat;
  background-size: cover;
  background-attachment: fixed;
}

.title {
  position: absolute;
  left: 100px;
  top: 20px;
  font-size: 40px;
}

.box2 .historyTr {
  position: relative;
  left: 450px;
  top: 30px;
  margin-bottom: 30px;
  font-family: "FangSong";
  font-size: 18px;
}

.box2 .head {
  position: relative;
  top: 10px;
  width: 850px;
  height: 30px;
  padding-top: 3px;
  border: solid 1px blanchedalmond;
  white-space: normal;
  word-break: break-all;
}

.box2 .body {
  position: relative;
  top: 10px;
  width: 850px;
  white-space: normal;
  word-break: break-all;
}

.box2 .trDate {
  position: relative;
  left: 620px;
  top: 10px;
  width: 200px;
}
</style>